/**
 * Internal dependencies
 */
import ExifData from './exif-data';

const {
	blockEditor: {
		InspectorControls,
	},
	components: {
		PanelBody,
		ToggleControl,
	},
	compose: {
		createHigherOrderComponent,
	},
	element: {
		Fragment,
	},
	i18n: {
		__,
	},
} = wp;

/**
 * Insert new attributes into block.
 *
 * @param {Object} settings block settings.
 * @param {string} name block name.
 * @return {Object} settings with updated attributes.
 */
const insertNewImgAttributes = ( settings, name ) => {
	if ( 'core/image' !== name ) {
		return settings;
	}

	const {
		attributes,
	} = settings;

	// Update attributes.
	settings.attributes = {
		...attributes, // copy original attributes.
		// ...exifAttributes, // copy new attributes.
		exifDataToggle: {
			type: 'boolean',
			default: false,
		},
		exifData: {
			type: 'object',
			default: {},
		}
	};

	return settings;
};

const withInspectorControls = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {
		const {
			attributes: {
				exifDataToggle,
				exifData,
				id,
			},
			setAttributes,
		} = props;

		const insertClassName = exifDataToggle ? 'exif-data-enabled' : '';

		const onToggleChange = ( newValue ) => {
			setAttributes( { exifDataToggle: newValue } );
		};

		// Update exifData att.
		const updateExifData = ( newValue ) => {
			setAttributes( { exifData: newValue } );
		};

		return (
			<Fragment>
				<div className={ insertClassName }>
					<BlockEdit { ...props } />
					{ exifDataToggle && id ?
					<ExifData
						id={ id }
						exifData={ exifData }
						onChange={ updateExifData }
					/>
					: '' }
				</div>
				<InspectorControls>
					<PanelBody
						title={ __( 'Enable Exif Data' ) }
					>
						<ToggleControl
							label={ __( 'Display exif data from image file.' ) }
							checked={ exifDataToggle }
							onChange={ onToggleChange }
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
		);
	};
}, 'withInspectorControl' );

/**
 * Modify the block save function.
 *
 * @param {Object} el
 * @param {Object} block data.
 * @param {Object} attributes from block.
 * @return {Object} updated element object.
 */
const modifySavedElement = ( el, block, attributes ) => {
	const {
		exifDataToggle,
	} = attributes;

	// Return early if not image block or if exifData is false.
	if ( 'core/image' === block.className ) {
		return el.props.className = exifDataToggle ? 'expecto-patronum' : '';
	} else {
		return el;
	}
};

// Set new attributes.
wp.hooks.addFilter(
	'blocks.registerBlockType',
	'core/image',
	insertNewImgAttributes
);

// Insert editor control.
wp.hooks.addFilter(
	'editor.BlockEdit',
	'core/image',
	withInspectorControls
);

// Modify the saved value.
wp.hooks.addFilter(
	'blocks.getSaveElement',
	'core/image',
	modifySavedElement
);
